import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { DetailPage } from "../detail-page/detail-page";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  devices = [
    {"name":"nuk","brand":"intel","type":"microcomputer","quantity":"1"},
    {"name":"pi zero","brand":"raspberry","type":"microcomputer","quantity":"3"},
    {"name":"xu4","brand":"odroid","type":"microcomputer","quantity":"5"}
  ];
  constructor(public navCtrl: NavController) {

  }
  tapEvent($event, device) {
    this.navCtrl.push(DetailPage, device);
  }
  getItems(ev: any) {
    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.devices = this.devices.filter((item) => {
        return (item);
      })
    }
  }

}



